const LogoCarouselAnt = () => (
	<div className="carousel_container">
	  <Carousel autoplay dots={false}>
	    { LOGOS.map((item, index) => <LogoRow {...item} key={item.id} />) }
	  </Carousel>
	</div>
)

class LogoCarousel extends Component {
	state = { 
		stage: 0,
		logos: shuffleArray(LOGOS)
	}

	tick = () => {
		var stage = this.state.stage
		var changes = {
			stage: (stage + 1) % 3
		}
		if (stage == 2) {
			changes.logos = shuffleArray(LOGOS)
		}

		this.setState(changes, () => console.log("Stage", this.state.stage))
	}

	componentDidMount = () => {
		setInterval(() => this.tick(), 3000)
	}

	render = () => { 
		var increment = Math.floor(this.state.logos.length / 3)
		var logos = this.state.logos.slice(this.state.stage * increment, (this.state.stage + 1) * increment)
		return (
			<div className="carousel_container">
				<br/>
				<p className="white_text tightText">
          Compatible with most stores, including
        </p>
        <div className="carousel_row">
				  { (this.state.stage === 0) && <Fade right>
				    { logos.map((item, index) => <LogoRow image={item} key={item + this.state.stage.toString()} />) }
				  </Fade>
					}
					{ (this.state.stage === 1) && <Fade right>
				    { logos.map((item, index) => <LogoRow image={item} key={item + this.state.stage.toString()} />) }
				  </Fade>
					}
					{ (this.state.stage === 2) && <Fade right>
				    { logos.map((item, index) => <LogoRow image={item} key={item + this.state.stage.toString()} />) }
				  </Fade>
					}
				</div>
			</div>
	)}
}

export default LogoCarousel