import React, { Component } from 'react';
import '../App.css';
import { Alert, Button, Col, Form, Input, Row } from 'antd';
import { Fade } from 'react-reveal'; 
import { scrollToTarget, validateEmail } from '../helpers/utils.js'
import MailchimpSubscribe from "react-mailchimp-subscribe"
 
const MAILCHIMP_URL = "https://tryboppity.us20.list-manage.com/subscribe/post?u=b65be31ac5aaee261d4390f82&amp;id=966ebad2e5"

const MailchimpForm = () => (
  <MailchimpSubscribe
    url={MAILCHIMP_URL}
    render={({ subscribe, status, message }) => (
      <div className='early_access_form'>
        <EarlyAccessForm onSubmitted={formData => subscribe(formData)} />
	        {status === "sending" && <Fade bottom><Alert message="Checking eligibility..." type="warning" showIcon /></Fade>}
	        {status === "error" && <Fade bottom><Alert message="Oh no! Something's not right!" type="error" showIcon /></Fade>}
	        {status === "success" && <Fade bottom><Alert message="Your exclusive invite is on its way!" type="success" showIcon /></Fade>}
      </div>
    )}
  />
)
 
class EarlyAccessForm extends Component {
	state={
		failedSubmit: false
	}

	onSubmit = () => {
		
		if (this.validateEmailStatus(this.state.email)) {
			this.setState({ success: true, failedSubmit: false }, () => {
				scrollToTarget('survey', 0)
				// Submit email here
			})
			this.props.onSubmitted({ EMAIL: this.state.email })
		}
		else {
			console.log("Failed on", this.state.email)
			this.setState({ failedSubmit: true })
		}
	}

  validateEmailStatus = (email) => {
  	if (email == null) {
      return null
    }
    if (email.length <= 0) {
      return null
    }
    return validateEmail(email)
  }

	render() {
		return (
			<Row gutter={10} justify='space-around'>
				<Col sm={24} md={16} style={styles.col}>
					<Input 
						value={this.state.email}
						onChange={(e) => this.setState({ email: e.target.value })} 
						size="large" 
						placeholder="Your best email" />
				</Col>
				<Col sm={24} md={8} style={styles.col}>
					<Button 
						onClick={this.onSubmit}
						block 
						htmlType="submit"
						type="primary" 
						size='large' 
						style={styles.button}
						className='purple_pink_bg' 
						id='primary-gradient'>
						GET EARLY ACCESS
					</Button>
				</Col>
			</Row>			
		)
	}
}

const styles = {
	col: {
		marginBottom: 10
	},
	button: {
		fontWeight: 'bold'
	}
}

export default MailchimpForm