import React, { Component } from 'react';
import '../App.css';
import { Button, Col, Card, Icon, Input, Tabs, Row } from 'antd';
import { PINK, GREY, PURPLE } from '../helpers/Colors.js'
const TabPane = Tabs.TabPane;
const { Meta } = Card;

const Circle = ({ color, size }) => <div style={{height: size, width: size, borderRadius: size / 2, backgroundColor: color}}/>

Circle.defaultProps = {
	color: PURPLE,
	size: 10
}

const Q1_OPTIONS = [
	{
		id: 'shirts',
		title: "Shirts and pants",
		image: "/images/shirts_pants.png"
	},
	{
		id: 'dresses',
		title: "Dresses",
		image: "/images/dresses.png"
	},
	{
		id: 'suits',
		title: "Suits",
		image: "/images/Suits.png"
	},
	{
		id: 'all',
		title: "Every time I shop!",
		image: "/images/closet.png"
	}
]

const Q2_OPTIONS = [
	{
		id: 'tight',
		title: "Tight",
		description: "I often wish clothes were cut slimmer and tighter.  Bodycon is a fave!"
	},
	{
		id: 'regular',
		title: "Regular",
		description: "Not too tight or too loose. The goldilocks of fits. Keep it simple."
	},
	{
		id: 'loose',
		title: "Loose",
		description: "Comfort and flexibility are priorities. I like things to be a little oversized."
	}
]

export default class SurveyForm extends Component {
	state = {
		stage: '1',
		answers: {}
	}

	answerQuestion = (id, val) => {
		this.setState({ 
			answers: { ...this.state.answers, [id]: val },
			stage: (parseInt(this.state.stage) + 1).toString()
		}, () => {
			if (parseInt(this.state.stage) >= 3) {
				this.onFinish()
			}
		})
	}

	onFinish = () => {
		console.log("finished on", this.state.answers)
	}

	render() {
		return (
			<div id="survey" className="survey_form">
				<Tabs 
					tyle='card'
					tabBarStyle={styles.tabBar}
					tabPosition='bottom' 
					activeKey={this.state.stage}
					onChange={(stage) => this.setState({stage})}>
          <TabPane tab={<Circle color={this.state.stage == '1' ? PURPLE : GREY }/>} key="1">
	          <h1>
			        I'd like <span className="purple">unlimited tailoring</span> for...
			      </h1>
			      <p>
			        Whether it’s formal wear or every time you shop, what do you want tailored?
			      </p>
						<Row gutter={16} className="center_row" justify='space-around'>
							{ Q1_OPTIONS.map((item, index) => 
							<Col 
								key={item.id}
								xs={12} 
								sm={12} 
								md={6} 
								onClick={() => this.answerQuestion('q1', item.id)}
								justify='center' 
								className="card_cols">
								<Card
									size="small"
							    hoverable
							    style={{ width: '100%' }}
							    bodyStyle={styles.cardBody}
							    cover={
							    	<div 
							    		style={{backgroundImage: `url(${item.image})` }}  
							    		className="square"/>
							    }
							  >
							  	<div className="card_textbox">
								    <h4>
								    	{item.title}
								    </h4>
								  </div>
							  </Card>
							</Col> )}
        		</Row>

          </TabPane>
          <TabPane tab={<Circle color={this.state.stage == '2' ? PURPLE : GREY }/>} key="2" disabled={!this.state.answers.q1}>
	          <h2 className="purple">
			        my preferred fit
			      </h2>
			      <p>
			        You’ll never have to guess about sizing up or down again.
			      </p>
			      <Row gutter={16} className="center_row" justify='space-around'>
							{ Q2_OPTIONS.map((item, index) => 
							<Col 
								sm={24} 
								md={8} 
								onClick={() => this.answerQuestion('q2', item.id)}
								key={item.id} 
								justify='center' 
								className="card_cols">
								<Card
							    hoverable
							    style={{ width: '100%' }}
							  >
							    <Meta
							      title={item.title}
							      description={item.description}
							    />
							  </Card>
							</Col> )}
        		</Row>
          </TabPane>
          <TabPane tab={<Circle color={this.state.stage == '3' ? PURPLE : GREY }/>} key="3" disabled={!this.state.answers.q2}>
	          <h1 className="purple">
			        Stay tuned
			      </h1>
			      <p>
			      	We'll update you as we get through the waitlist!
			      </p>
			      
          </TabPane>
        </Tabs>
			</div>
		)
	}
}

const styles = {
	tabBar: {
		borderWidth: 0
	},
	cardBody: {
		padding: 0
	}
}